import { Box, Stack, Typography, Card, CardHeader, Avatar, CardMedia, CardContent } from "@mui/material";

export default function Display(props) {
    return (
        <Box id={props.id} sx={{minHeight: '50vh', p: 2}}>
            <Typography variant="h3" gutterBottom>{props.title}</Typography>
            <Stack direction={{ xs: 'column', md: 'row' }} spacing={3} alignItems={{ xs: 'center', md: 'unset' }} justifyContent="center">
            {props.items.map(x => (
                <Card key={x.title} sx={{maxWidth: 400}}>
                {props.type && props.type === 'article' && (
                    <CardHeader
                        title={x.title}
                        subheader={x.date}
                        avatar={
                            <Avatar sx={{ bgcolor: 'error.main', color: 'text.secondary' }} >
                                {x.name.split(' ').map(x => x.charAt(0)).join('').toUpperCase()}
                            </Avatar>
                        }
                    />
                )}
                <CardMedia
                    component="img"
                    height="180"
                    image={x.img}
                    alt={x.name}
                />
                <CardContent>
                    {(!props.type || props.type !== 'article') && (
                        <Typography gutterBottom variant="h5" component="div">
                        {x.title}
                        </Typography>
                    )}
                    <Typography variant="body2" color="text.secondary">
                    {x.text}
                    </Typography>
                </CardContent>
                </Card>
            ))}
            </Stack>
        </Box>
    );
}