import React from 'react';
import { useSpring, useChain, useSpringRef, animated } from '@react-spring/web'

import { styled } from '@mui/material/styles';
import { Box, Typography, Link } from '@mui/material';

import KeyboardDoubleArrowDownIcon from '@mui/icons-material/KeyboardDoubleArrowDown';

import CursorText from './CursorText';

const Jumbotron = styled(animated.div)(({theme}) => ({
  minHeight: '100vh',
  boxSizing: 'border-box',
  display: 'flex',
  position: 'relative',
  justifyContent: 'center',
  alignItems: 'center'
}));

const CenterBox = styled(animated.div)(({theme}) => ({
  maxWidth: '80em', 
  width: '100%',
  margin: '2em'
}));

const AnimatedDownArrow = ({ springref }) => {
  const styles = useSpring({
    loop: true,
    from: { y: 0 },
    to: [
      { y: -30 },
      { y: 0 }
    ],
    config: { duration: 800, friction: 5 },
    ref: springref
  })

  return (
    <animated.div style={styles}>
      <KeyboardDoubleArrowDownIcon sx={{ fontSize: '4em', color: 'text.primary' }} />
    </animated.div>
  );
}

export default function Splash(props) {
  const [cursorStart, setCursorStart] = React.useState(false);
  const clickHandler = (e) => props.headerRef.current.scrollIntoView({
    behavior: "smooth"
  });

  const arrowRef = useSpringRef();

  // create fade in of background
  const springRef = useSpringRef();
  const fadeinStyle = useSpring({
    to: { opacity:  1 },
    from: {opacity: 0},
    config: { duration: 800 },
    ref: springRef
  });

  const fadeRiseRef = useSpringRef();
  const fadeRiseStyle = useSpring({
    opacity: 1,
    y: 0,
    from: { opacity: 0, y: 100 },
    config: { duration: 600 },
    ref: fadeRiseRef,
    onRest: () => setCursorStart(true)
  })

  const arrowRiseRef = useSpringRef();
  const arrowRiseStyle = useSpring({
    opacity: 1,
    y: 0,
    from: { opacity: 0, y: 100 },
    config: { duration: 600 },
    ref: arrowRef
  })

  useChain([springRef, fadeRiseRef, arrowRiseRef, arrowRef]);

  return ( 
    <Jumbotron 
      style={fadeinStyle}
      sx={{
        background: `url('${props.background}')`, 
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed'
      }}>

        <CenterBox style={fadeRiseStyle}>
          <Typography sx={(theme) => ({ fontSize: { xs: '3rem', sm: theme.typography.h1.fontSize}})} color="text.primary" variant="h1">
            {props.title}
          </Typography>
          <CursorText start={cursorStart}>{props.subtitle}</CursorText>
        </CenterBox>

          <Box sx={{position: 'absolute', bottom: '2em', cursor: 'pointer' }} onClick={clickHandler}>
            <animated.div style={arrowRiseStyle}>
              <AnimatedDownArrow springref={arrowRef} />
            </animated.div>
          </Box>
        
        <Box sx={{position: 'absolute', bottom: 0, right: 0, background: 'rgba(255, 255, 255, 0.1)' }}>
          {props.links.map(l => (
            <Box key={l.name} m={2} textAlign='center'>
              <Link color="text.primary" href={l.link} underline="hover" target="_blank" rel="noopener">
                <l.icon sx={{color: 'text.primary', fontSize: '1.5em'}} />
                <Typography color="text.primary" >{l.name}</Typography>
              </Link>
            </Box>
          ))}
        </Box>

    </Jumbotron>
  );
}

