import React from 'react';

import { Box, Typography, Avatar, Link, Card, Chip, useMediaQuery } from '@mui/material';
import { useTheme, styled } from '@mui/material/styles';
import { Timeline, TimelineItem, TimelineSeparator, TimelineConnector, TimelineContent, TimelineDot } from '@mui/lab';

import OpenInNewIcon from '@mui/icons-material/OpenInNew';

const CenteredBox = styled(Box)(({ theme }) => ({
  display: 'flex',
  justifyContent: 'center',
  alignItems: 'center'
}));

const TimelineList = styled('ul')(({ theme }) => ({
  paddingLeft: theme.spacing(2),
  listStyle: 'disc'
}));

const getColour = (i) => {
  const arr = ["primary", "error", "warn", "success", "info"];
  return arr[arr.length % i]; 
}
  

export default function CustomTimeline(props) {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));

  return (
    <Box id={props.id} sx={{minHeight: '50vh', p: 2}}>
      <Typography variant="h3" gutterBottom>History</Typography>
      <CenteredBox>
      
      <Timeline
      position={isMobile ? "right" : "alternate"}
      sx={{ 
        maxWidth: isMobile ? '80em' : '160em', 
        '& .MuiTimelineItem-missingOppositeContent::before': {
          content: isMobile ? 'unset' : '""' 
        }
      }}>
        {props.items.map((x, i) => {
          const colour = getColour(i);

          return (
            <TimelineItem key={x.name}>
              <TimelineSeparator>
                <TimelineConnector />
                  <Link href={x.link} target="_blank" rel="noopener">
                    <TimelineDot color={colour}>
                      <Avatar sx={{ width: '3em', height: '3em' }} src={x.img} alt={x.name.split(' - ')[0]} />
                    </TimelineDot>
                  </Link>
                <TimelineConnector />
              </TimelineSeparator>
              <TimelineContent sx={{ py: '12px', px: 2 }}>
                <Card sx={{p: 2}}>
                  <Link display="block" underline="hover"  href={x.link} target="_blank" rel="noopener" color="text.primary">
                    <Typography variant="h6" component="span" color="text.primary">
                      {x.name}
                    </Typography>
                    <OpenInNewIcon sx={{pl: 1}} fontSize="small" />
                  </Link>
                  <Chip sx={{my: 1}} label={x.date} color={colour} />
                  <TimelineList>
                    {x.details.map((d, j) => (
                      <Typography key={j} component="li" variant="body1">{d}</Typography>
                    ))}
                  </TimelineList>
                </Card>
              </TimelineContent>
            </TimelineItem>
          );
        })}
      </Timeline>
    </CenteredBox>
    </Box>
  );
}