import React from "react";

import { HashLink } from 'react-router-hash-link';

import { useTheme, styled } from '@mui/material/styles';
import { Box, AppBar, Toolbar, Typography, Tabs, Tab, Hidden } from '@mui/material';

const TallTabs = styled(Tabs)(({ theme }) => ({
    ...theme.mixins.toolbar,
    margin: 0,
    flexGrow: 1,
    '& .MuiTabs-flexContainer': {
        height: '100%'
    }
}));

const HeaderName = styled(Typography)(({ theme }) => ({
    position: 'absolute',
    color: theme.palette.text.primary,
    textDecoration: 'none',
    zIndex: 100
}));


function LinkTab(props) {
    const theme = useTheme();
    return (
      <Tab component={HashLink} to={props.link} scroll={(el) => scrollToOffset(el, theme)} {...props} />
    );
}

function scrollToOffset(element, theme){
    const headerOffset = theme.mixins.toolbar["@media (min-width:600px)"].minHeight;
    const elementPosition = element.getBoundingClientRect().top;
    const offsetPosition = elementPosition + window.scrollY - headerOffset;
  
    window.scrollTo({
        top: offsetPosition,
        behavior: "smooth"
    });
}

const Header = React.forwardRef((props, ref) => {
    const [value, setValue] = React.useState(0);
    const handleChange = (e, v) => setValue(v)

  return (
    <AppBar ref={ref} position="sticky" sx={{top: 0}}>
        <Toolbar>
            <Hidden smDown>
                <HeaderName variant="h6" component={HashLink} smooth to="#top">
                    {props.name}
                </HeaderName>
            </Hidden>
            <TallTabs value={value} onChange={handleChange} centered>
                {props.tabs.map(x => (
                    <LinkTab key={x.name} label={x.name} link={x.link} />
                ))}
            </TallTabs>
        </Toolbar>
    </AppBar>
    );
});

export default Header;