import React from 'react';
import { keyframes } from '@emotion/react'

import { Typography } from '@mui/material';
import { styled } from '@mui/material/styles';

const CursorTextFont = styled(Typography)(({ theme }) => ({
    fontFamily: 'Monospace',
    [theme.breakpoints.down('sm')]: {
        fontSize: '1.2rem'
    }
}));

export default function CursorText(props) {
    const [text, setText] = React.useState('');
    const [complete, setComplete] = React.useState(false);

    React.useEffect(() => {
            let interval = null;
            interval = setInterval(() => {
                if (!!props.start) {
                    const currentLength = text.length;
                    setComplete(currentLength === props.children.length);
        
                    if (currentLength < props.children.length) {
                        setText(props.children.substring(0, currentLength + 1));
                    }
                }
            }, 150);
            return () => clearInterval(interval);
      }, [text, props.start]);

      const blink = keyframes`
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
      `;

    return (
        <CursorTextFont component="code" color="text.primary" variant="h4">
            {text}
            <CursorTextFont 
                color="text.primary"
                variant="h4"
                component="code" 
                ml={0.5}
                sx={complete ? { animation: `${blink} 1.5s steps(2) infinite` } : undefined }
                id="cursor">_</CursorTextFont>
        </CursorTextFont>
    );
}