import React from 'react';
import { keyframes } from '@emotion/react'

import { Typography, Box } from '@mui/material';
import { styled } from '@mui/material/styles';

const ClockFont = styled(Typography)(({ theme }) => ({
    fontFamily: 'Monospace',
    //fontSize: "3rem",
    fontSize: theme.typography.h1.fontSize,
    [theme.breakpoints.down('sm')]: {
        fontSize: '3rem'
    }
}));

const addZero = (i) => (i < 10) ? "0" + i : i;
const day = (i) => ["Sunday", "Monday", "Tuesday", "Wednesday", "Thursday", "Friday", "Saturday"][i];
const month = (i) => ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"][i];
const th = (i) => {
    switch (i) {
        case '1': case '21': case '31': return i + "st";
        case '2': case '22': return i + "nd";
        case '3': case '23': return i + "st";
        default: return i + "th";
    }
}
export default function Clock(props) {
    const [time, setTime] = React.useState(new Date());

    React.useEffect(() => {
        let interval = null;
        setInterval(() => setTime(new Date()), 500); 
        return () => clearInterval(interval);
      }, []);

      const blink = keyframes`
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
      `;

    return (
        <Box sx={{fontSize: '3rem'}}>
            <ClockFont component="span">{addZero(time.getHours())}</ClockFont>
            <ClockFont component="span" sx={{ animation: `${blink} 2s steps(2) infinite` }}>:</ClockFont>
            <ClockFont component="span">{addZero(time.getMinutes())}</ClockFont>
            <ClockFont component="span" sx={{ animation: `${blink} 2s steps(2) infinite` }}>:</ClockFont>
            <ClockFont component="span">{addZero(time.getSeconds())}</ClockFont>
            <Typography variant="h4">{`${day(time.getDay())} ${th(time.getDate())} ${month(time.getMonth())}, ${time.getFullYear()}`}</Typography>
        </Box>
    );
}