import React from 'react';
import { useSpring, useChain, useSpringRef, animated } from '@react-spring/web'

import { styled } from '@mui/material/styles';
import { Box, Typography, Button, Link } from '@mui/material';

import GitHubIcon from '@mui/icons-material/GitHub';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';

import Clock from '../Components/Clock';

const Jumbotron = styled(animated.div)(({theme}) => ({
  minHeight: '100vh',
  boxSizing: 'border-box',
  display: 'flex',
  position: 'relative',
  justifyContent: 'center',
  alignItems: 'center'
}));

const CenterBox = styled(animated.div)(({theme}) => ({
  maxWidth: '80em', 
  width: '100%',
  margin: '2em'
}));

const FadedButton = styled(Button)(({theme}) => ({
    position: 'absolute',
    top: '2em',
    left: '2em',
    borderWidth: 2,
    borderStyle: 'solid',
    borderColor: theme.palette.primary.main,
    borderRadius: 5,
    opacity: 0.2,
    transition: 'opacity 0.5s',
    '&:hover': {
        opacity: 1
    }
}));
  

const externalLinks = [
    {name: 'Email', icon: EmailIcon, link: 'mailto:z.dowsett@outlook.com'},
    {name: 'GitHub', icon: GitHubIcon, link: 'https://www.github.com/ZakDaMack'},
    {name: 'LinkedIn', icon: LinkedInIcon, link: 'https://www.linkedin.com/in/zak-dowsett-4a7455131/'},
];

export default function Focus(props) {

  // create fade in of background
  const springRef = useSpringRef();
  const fadeinStyle = useSpring({
    to: { opacity:  1 },
    from: {opacity: 0},
    config: { duration: 800 },
    ref: springRef
  });

  const fadeRiseRef = useSpringRef();
  const fadeRiseStyle = useSpring({
    opacity: 1,
    y: 0,
    from: { opacity: 0, y: 100 },
    config: { duration: 600 },
    ref: fadeRiseRef
  })


  useChain([springRef, fadeRiseRef]);

  return ( 
    <Jumbotron 
      style={fadeinStyle}
      sx={{
        background: `url('mountains-new.jpg')`, 
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        backgroundAttachment: 'fixed'
      }}>
          
          <FadedButton href="/">
            <ArrowBackIcon sx={{fontSize: '3rem'}}/>
          </FadedButton>

        <CenterBox style={fadeRiseStyle}>
          <Clock />
        </CenterBox>
        
        <Box sx={{position: 'absolute', bottom: 0, right: 0, background: 'rgba(255, 255, 255, 0.1)' }}>
          {externalLinks.map(l => (
            <Box key={l.name} m={2} textAlign='center'>
              <Link color="text.primary" href={l.link} underline="hover" target="_blank" rel="noopener">
                <l.icon sx={{color: 'text.primary', fontSize: '1.5em'}} />
                <Typography color="text.primary" >{l.name}</Typography>
              </Link>
            </Box>
          ))}
        </Box>

    </Jumbotron>
  );
}